"use client"

import React, { useState, useEffect } from 'react'
import { useUser } from '@clerk/nextjs'
import { useRouter } from 'next/navigation'
import { Sparkles, Star, Trophy, Clock, BookOpen } from 'lucide-react'

export default function DashboardPage() {
  const { user, isLoaded } = useUser()
  const router = useRouter()
  const [currentTime, setCurrentTime] = useState(new Date())
  const [animatedModules, setAnimatedModules] = useState<number[]>([])

  // Actualizar hora cada segundo
  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentTime(new Date())
    }, 1000)
    return () => clearInterval(timer)
  }, [])

  // Animación secuencial de módulos
  useEffect(() => {
    const animateModules = () => {
      modules.forEach((_, index) => {
        setTimeout(() => {
          setAnimatedModules(prev => [...prev, index])
        }, index * 200)
      })
    }
    
    const timer = setTimeout(animateModules, 500)
    return () => clearTimeout(timer)
  }, [])

  const modules = [
    {
      id: 'conjuntos',
      name: 'CONJUNTOS',
      description: 'Agrupa elementos',
      icon: '🔵🔴',
      color: 'from-blue-400 to-purple-500',
      bgColor: 'bg-gradient-to-br from-blue-100 to-purple-100',
      borderColor: 'border-blue-300',
      route: '/modules/conjuntos'
    },
    {
      id: 'numeracion',
      name: 'NUMERACIÓN',
      description: 'Aprende números',
      icon: '0️⃣2️⃣4️⃣6️⃣8️⃣',
      color: 'from-yellow-400 to-orange-500',
      bgColor: 'bg-gradient-to-br from-yellow-100 to-orange-100',
      borderColor: 'border-yellow-300',
      route: '/modules/numeracion'
    },
    {
      id: 'suma',
      name: 'SUMA',
      description: 'Suma fácil',
      icon: '➕',
      iconStyle: 'text-6xl',
      color: 'from-green-400 to-emerald-500',
      bgColor: 'bg-gradient-to-br from-green-100 to-emerald-100',
      borderColor: 'border-green-300',
      route: '/modules/suma'
    },
    {
      id: 'resta',
      name: 'RESTA',
      description: 'Resta simple',
      icon: '➖',
      iconStyle: 'text-6xl',
      color: 'from-red-400 to-pink-500',
      bgColor: 'bg-gradient-to-br from-red-100 to-pink-100',
      borderColor: 'border-red-300',
      route: '/modules/resta'
    },
    {
      id: 'multiplicacion',
      name: 'MULTIPLICACIÓN',
      description: 'Multiplica',
      icon: '✖️',
      iconStyle: 'text-6xl',
      color: 'from-indigo-400 to-purple-500',
      bgColor: 'bg-gradient-to-br from-indigo-100 to-purple-100',
      borderColor: 'border-indigo-300',
      route: '/modules/multiplicacion'
    },
    {
      id: 'division',
      name: 'DIVISIÓN',
      description: 'Divide y reparte',
      icon: '➗',
      iconStyle: 'text-6xl',
      color: 'from-teal-400 to-cyan-500',
      bgColor: 'bg-gradient-to-br from-teal-100 to-cyan-100',
      borderColor: 'border-teal-300',
      route: '/modules/division'
    },
    {
      id: 'geometria',
      name: 'GEOMETRÍA',
      description: 'Formas divertidas',
      icon: '🔷🔶🔴',
      color: 'from-pink-400 to-rose-500',
      bgColor: 'bg-gradient-to-br from-pink-100 to-rose-100',
      borderColor: 'border-pink-300',
      route: '/modules/geometria'
    }
  ]

  const getGreeting = () => {
    const hour = currentTime.getHours()
    if (hour < 12) return "Buenos días"
    if (hour < 18) return "Buenas tardes"
    return "Buenas noches"
  }

  const handleModuleClick = (moduleRoute: string) => {
    // Sonido de clic
    if (typeof window !== 'undefined' && window.playSound) {
      window.playSound('correct-sound')
    }
    router.push(moduleRoute)
  }

  if (!isLoaded) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-yellow-50 to-orange-50">
        <div className="text-center">
          <div className="animate-spin w-16 h-16 border-4 border-yellow-400 border-t-transparent rounded-full mx-auto mb-4"></div>
          <p className="text-lg text-gray-600">Cargando tu aventura matemática...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-amber-50 via-orange-50 to-yellow-50 relative overflow-hidden">
      {/* Elementos decorativos de fondo */}
      <div className="absolute inset-0 pointer-events-none">
        <div className="absolute top-10 left-10 w-32 h-32 bg-yellow-200 rounded-full opacity-20 blur-xl animate-pulse"></div>
        <div className="absolute top-32 right-20 w-24 h-24 bg-blue-200 rounded-full opacity-20 blur-xl animate-bounce" style={{animationDelay: '1s'}}></div>
        <div className="absolute bottom-20 left-32 w-28 h-28 bg-pink-200 rounded-full opacity-20 blur-xl animate-pulse" style={{animationDelay: '2s'}}></div>
        <div className="absolute bottom-32 right-10 w-20 h-20 bg-green-200 rounded-full opacity-20 blur-xl animate-bounce" style={{animationDelay: '0.5s'}}></div>
      </div>

      {/* Header con saludo personalizado */}
      <header className="relative z-10 p-6">
        <div className="max-w-7xl mx-auto">
          <div className="flex justify-between items-center mb-8">
            <div className="flex items-center gap-4">
              <div className="relative">
                <img
                  src="/images/logo.png"
                  alt="MentaMática"
                  className="w-12 h-12 object-contain animate-float"
                />
                <div className="absolute -top-1 -right-1 w-4 h-4 bg-yellow-400 rounded-full animate-ping"></div>
              </div>
              <div>
                <h1 className="text-3xl font-bold text-gray-800">MentaMática</h1>
                <p className="text-sm text-gray-600">¡Aprende jugando!</p>
              </div>
            </div>

            {/* Stats del usuario */}
            <div className="flex items-center gap-4">
              <div className="flex items-center gap-2 bg-white/80 backdrop-blur-sm rounded-full px-4 py-2 shadow-lg">
                <Star className="w-5 h-5 text-yellow-500" />
                <span className="font-bold text-gray-700">120</span>
              </div>
              <div className="flex items-center gap-2 bg-white/80 backdrop-blur-sm rounded-full px-4 py-2 shadow-lg">
                <Trophy className="w-5 h-5 text-orange-500" />
                <span className="font-bold text-gray-700">5</span>
              </div>
            </div>
          </div>

          {/* Saludo personalizado */}
          <div className="text-center mb-12">
            <h2 className="text-4xl font-bold text-gray-800 mb-4 animate-fade-in">
              {getGreeting()}, {user?.firstName || 'Matemático'}! 👋
            </h2>
            <p className="text-xl text-gray-600 animate-fade-in-delay">
              ¿Qué quieres aprender hoy?
            </p>
          </div>
        </div>
      </header>

      {/* Grid de módulos */}
      <main className="relative z-10 px-6 pb-12">
        <div className="max-w-6xl mx-auto">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-8">
            {modules.map((module, index) => (
              <div
                key={module.id}
                className={`
                  group cursor-pointer transform transition-all duration-500 hover:scale-105
                  ${animatedModules.includes(index) ? 'animate-slide-up opacity-100' : 'opacity-0 translate-y-8'}
                `}
                style={{animationDelay: `${index * 0.1}s`}}
                onClick={() => handleModuleClick(module.route)}
              >
                <div className={`
                  ${module.bgColor} ${module.borderColor} 
                  border-4 rounded-3xl p-8 shadow-xl hover:shadow-2xl
                  backdrop-blur-sm relative overflow-hidden
                  transition-all duration-300 group-hover:-translate-y-2
                `}>
                  {/* Efecto de brillo */}
                  <div className="absolute inset-0 bg-gradient-to-r from-white/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
                  
                  {/* Partículas flotantes */}
                  <div className="absolute top-2 right-2 text-xl animate-bounce opacity-50 group-hover:opacity-100" style={{animationDelay: `${index * 0.2}s`}}>✨</div>
                  
                  {/* Contenido del módulo */}
                  <div className="relative z-10 text-center">
                    {/* Icono animado */}
                    <div className="mb-6 h-24 flex items-center justify-center">
                      <div className={`${module.iconStyle || 'text-5xl'} transform group-hover:scale-110 transition-transform duration-300 group-hover:animate-pulse`}>
                        {module.icon}
                      </div>
                    </div>

                    {/* Nombre del módulo */}
                    <h3 className="text-xl font-bold text-gray-800 mb-2 group-hover:text-gray-900">
                      {module.name}
                    </h3>
                    
                    {/* Descripción */}
                    <p className="text-sm text-gray-600 mb-4 group-hover:text-gray-700">
                      {module.description}
                    </p>

                    {/* Indicador de progreso */}
                    <div className="w-full bg-gray-200 rounded-full h-2 mb-4">
                      <div 
                        className={`bg-gradient-to-r ${module.color} h-2 rounded-full transition-all duration-1000 group-hover:animate-pulse`}
                        style={{width: `${Math.random() * 60 + 20}%`}}
                      ></div>
                    </div>

                    {/* Botón de acción */}
                    <div className={`
                      bg-gradient-to-r ${module.color} text-white 
                      font-bold py-2 px-6 rounded-full shadow-lg
                      transform group-hover:scale-105 transition-all duration-300
                      group-hover:shadow-xl
                    `}>
                      ¡Jugar!
                    </div>
                  </div>

                  {/* Decoraciones adicionales */}
                  <div className="absolute bottom-2 left-2 text-sm opacity-30 group-hover:opacity-60 transition-opacity">
                    🎯
                  </div>
                  <div className="absolute top-2 left-2 text-sm opacity-30 group-hover:opacity-60 transition-opacity">
                    🌟
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </main>

      {/* Footer motivacional */}
      <footer className="relative z-10 text-center py-8">
        <div className="bg-white/60 backdrop-blur-sm rounded-full mx-auto max-w-md py-4 px-8 shadow-lg">
          <p className="text-gray-700 font-medium flex items-center justify-center gap-2">
            <Sparkles className="w-5 h-5 text-yellow-500 animate-spin" />
            ¡Cada problema resuelto te hace más inteligente!
            <Sparkles className="w-5 h-5 text-yellow-500 animate-spin" />
          </p>
        </div>
      </footer>

      <style jsx>{`
        @keyframes float {
          0%, 100% { transform: translateY(0px); }
          50% { transform: translateY(-10px); }
        }
        
        @keyframes fade-in {
          from { opacity: 0; transform: translateY(20px); }
          to { opacity: 1; transform: translateY(0); }
        }
        
        @keyframes fade-in-delay {
          from { opacity: 0; transform: translateY(20px); }
          to { opacity: 1; transform: translateY(0); }
        }
        
        @keyframes slide-up {
          from { opacity: 0; transform: translateY(30px) scale(0.9); }
          to { opacity: 1; transform: translateY(0) scale(1); }
        }
        
        .animate-float {
          animation: float 3s ease-in-out infinite;
        }
        
        .animate-fade-in {
          animation: fade-in 1s ease-out;
        }
        
        .animate-fade-in-delay {
          animation: fade-in-delay 1s ease-out 0.3s both;
        }
        
        .animate-slide-up {
          animation: slide-up 0.6s ease-out forwards;
        }
      `}</style>
    </div>
  )
}